import React, { useState, useEffect } from 'react'
import { analytics } from './Auth/Config'
import { collection, doc, getDocs, addDoc, updateDoc, deleteDoc } from 'firebase/firestore'

function App() {
  const [user, setUser] = useState([])
  const [tittle, setTittle] = useState("")
  const [desc, setDesc] = useState("")
  const [ntittle, setnTittle] = useState("")
  const [ndesc, setnDesc] = useState("")
  const userCollectionRef = collection(analytics, "users");
  let note = {
    name: tittle,
    desc: desc
  }
  const deleteUser = async (id) => {
    const userDoc = doc(analytics, "users", id);
    await deleteDoc(userDoc)
    await getUser();
  }
  const UpdateUser = async (id) => {
    const userDoc = doc(analytics, "users", id);
    let newNote = {
      name: ntittle,
      desc: ndesc
    }
    await updateDoc(userDoc, newNote)
    await getUser();
  }
  const Adddoc = async () => {
    await addDoc(userCollectionRef, note);

    await getUser();
    setTittle("");
    setDesc("");
  }
  const getUser = async () => {

    const data = await getDocs(userCollectionRef)
    setUser(data.docs.map((doc) => ({ ...doc.data(), id: doc.id })));
    console.log(data.docs);
  }
  useEffect(() => {
    getUser();
  }, [])
  return (
    <div className='container' >
      <div className="w-50 mx-auto">
        <h3 className='text-center'>Input Notes</h3>
        <div className="mb-3">
          <label htmlFor="exampleFormControlInput1" className="form-label">
            Enter youtr Tittle
          </label>
          <input
            value={tittle}
            onChange={e => setTittle(e.target.value)}
            type="text"
            className="form-control"
            id="exampleFormControlInput1"
            placeholder="Note tittle"
          />
        </div>
        <div className="mb-3">
          <label htmlFor="exampleFormControlTextarea1" className="form-label">
            Example textarea
          </label>
          <textarea
            value={desc}
            onChange={e => setDesc(e.target.value)}
            className="form-control"
            id="exampleFormControlTextarea1"
            rows={3}
            defaultValue={""}
          />
        </div>
        <button onClick={Adddoc} class="btn btn-primary">Submit</button>
      </div>
      <h1 className='text-center'>All Notes</h1>
      <div class="row">

        {user.map((u) => {

          return (
            <div class="col-sm-6 col-md-6 col-lg-4" >
              <div className="card mx-auto" >
                {/* <img src="..." className="card-img-top" alt="..." /> */}
                <div className="card-body">
                  <h5 className="card-title">{u.name}</h5>
                  <p className="card-text">
                    {u.desc}

                  </p>
                  <button className="btn btn-danger" onClick={() => { deleteUser(u.id) }}>
                    delete
                  </button>
                  <button className="btn btn-success" onClick={() => {
                    setnTittle(u.name);
                    setnDesc(u.desc)
                  }} data-bs-toggle="modal" data-bs-target="#staticBackdrop">Update</button>

                </div>
              </div>
              <div
                className="modal fade "
                id="staticBackdrop"
                data-bs-backdrop="static"
                data-bs-keyboard="false"
                tabIndex={-1}
                aria-labelledby="staticBackdropLabel"
                aria-hidden="true"
              >
                <div className=" modal-dialog modal-dialog-centered">
                  <div className="modal-content">
                    <div className="modal-header">
                      <h5 className="modal-title" id="staticBackdropLabel">
                        Modal title
                      </h5>
                      <button
                        type="button"
                        className="btn-close"
                        data-bs-dismiss="modal"
                        aria-label="Close"
                      />
                    </div>
                    <div className="modal-body">
                      <div className="mb-3">
                        <label htmlFor="exampleFormControlInput1" className="form-label">
                          Enter youtr Tittle
                        </label>
                        <input
                          value={ntittle}
                          onChange={e => setnTittle(e.target.value)}
                          type="text"
                          className="form-control"
                          id="exampleFormControlInput1"
                          placeholder="Note tittle"
                        />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="exampleFormControlTextarea1" className="form-label">
                          Example textarea
                        </label>
                        <textarea
                          value={ndesc}
                          onChange={e => setnDesc(e.target.value)}
                          className="form-control"
                          id="exampleFormControlTextarea1"
                          rows={3}
                          defaultValue={""}
                        />
                      </div>
                    </div>
                    <div className="modal-footer">
                      <button
                        type="button"
                        className="btn btn-secondary"
                        data-bs-dismiss="modal"
                      >
                        Close
                      </button>
                      <button type="button" className="btn btn-primary" data-bs-dismiss="modal" onClick={() => UpdateUser(u.id)}>
                        Understood
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )
        })}
      </div>
    </div>
  );
}

export default App;
